'use strict';

/* global console */

import Saveable from 'module-crud-ui/app/scripts/crud.saveable.js';
import SegmentsBase from './med.segments.base.ctrl.js';

export default class SearchResultsTabsSegmentsCtrl extends Saveable  {

  /*@ngInject*/
  constructor(
    $injector,
    segmentService,
    squeezeService,
    segmentTransformerService,
    translateService,
    $state,
    $mdDialog,
    alertService,
    $scope){

    //noinspection JSAnnotator
    super(
      {
        injector: $injector,
        endpoint: 'mediaSegments',
        backToState: ".",
        key: 'id',
        successTitle: 'media.edit.alert.success.title',
        successMessage: 'media.edit.alert.success.message',
        successCreateMessage: 'media.edit.alert.create.success.message',
        successEditMessage: 'common.alert.edit.success.title',
        errorTitle: 'media.edit.alert.error',
        executeGet: false,
        entityDependenciesExecuteGet:false,
        entityDependencies: [
          {api: 'mediaSqueezeCreditsTypes', loading: 'isLoading', model: 'squeezeCreditTypes'},
          {api: 'networkResource', loading: 'isLoading', model: 'networkData'},
          {api: 'mediaTypeResource', loading: 'isLoading', model: 'types'}
        ]
      }
    );
    this.segmentsBase = new SegmentsBase(
      segmentService,
      squeezeService,
      segmentTransformerService,
      translateService,
      $state,
      $mdDialog,
      alertService,
      $scope
    );
    
    this.$state = $state;
    this.mdDialog = $mdDialog;
    this.alertService = alertService;
    this.segmentTransformerService = segmentTransformerService;
    this.translateService = translateService;
    this.segmentService = segmentService;
    this.squeezeService = squeezeService;

    if(!this.$state.params.id){
      this.isLoading=false;
      return;
    }
    this.segmentsBase.segmentEdit = {value: false};
    this.segmentsBase.segmentEditShow = {value: false};
    this.segmentsBase.squeezeCreate = {value: false};
    this.showContextMenu = {value: false};
    this.segmentsBase.action = [
      {
        tooltip: this.translateService.translate('tooltip.edit'),
        icon: 'edit',
        action: ()=>{this._onSegmentEdit();},
        show: this.segmentsBase.segmentEditShow
      },
      {
        tooltip: this.translateService.translate('tooltip.create'),
        icon: 'plus',
        action: ()=>{this._onSegmentCreate();},
        show: this.segmentsBase.segmentEdit
      }
    ];
    this.segmentsBase.squeezeCreditsEdit = {value: false};
    this.viewVirtualSegmentFlag = {value: false};
    this.segmentsBase.actionSqueezeCredits = [
      {
        tooltip: this.translateService.translate('tooltip.create'),
        icon: 'plus',
        action: ()=>{this._onSqueezeCreate();},
        show: this.segmentsBase.squeezeCreate
      }
    ];

    this.segmentsBase.contextMenu = [
      {
        label: this.translateService.translate('results.tabs.segments.create.virtual.segment'),
        action: () => {
          this.openCreateVirtualSegment();
        },
        show:this.showContextMenu
      },
      {
        label: this.translateService.translate('results.tabs.segments.view.virtual.segment'),
        action: () => {
          this.$state.go('viewVirtualSegment', {id: this.$state.params.id});
        },
        show: this.viewVirtualSegmentFlag
      }
    ];

    this.getFormats();
    this._getEntityDependencies();
    this.getMediaMaterial();
    this.getMedia();
    this._execute();
  }

  getMediaMaterial(){
    this.response = this.api['mediaMaterials'].get({'id': this.$state.params.id});
    this.response.isLoading = true;
    var objectData = this.response;

    this.response.$promise.then((response) => {

      objectData.isLoading = false;
      objectData.valid = true;
      this.mediaMaterial = response;

    }, function(error) {
      // Si nos devuelve un 404, no queremos que se muestre el recurso, pero tampoco que nos tire error
      if(error.status == 404){
        objectData.valid = false;
        objectData.isLoading = false;
      }
    });
  }

  getMedia(){
    this.response = this.api['medias'].get({'currentId': this.$state.params.currentId});
    this.response.isLoading = true;
    var objectData = this.response;

    this.response.$promise.then((response) => {

      objectData.isLoading = false;
      objectData.valid = true;
      this.media = response;

    }, function(error) {
      // Si nos devuelve un 404, no queremos que se muestre el recurso, pero tampoco que nos tire error
      if(error.status == 404){
        objectData.valid = false;
        objectData.isLoading = false;
      }
    });
  }


  /*
  * Esto es un horror, hay que refactorizarlo. PerdÃ³n, pero tenemos que terminar de cerrar otras cosas.
  * */
  getFormats(){
    this.response = this.api['mediaFormatResource'].get({'virtualSegmented':false});
    this.response.isLoading = true;
    var objectData = this.response;

    this.response.$promise.then((response) => {

      objectData.isLoading = false;
      objectData.valid = true;
      this.formats = response.content;
      this.getFormatsForLFID(response.content);

    }, function(error) {
      // Si nos devuelve un 404, no queremos que se muestre el recurso, pero tampoco que nos tire error
      if(error.status == 404){
        objectData.valid = false;
        objectData.isLoading = false;
      }
    });
    return this.response;
  }

  getFormatsForLFID(formats){
    let self = this;
    this.response = this.api['mediaFormatResource'].get({'virtualSegmented':true});
    this.response.isLoading = true;
    var objectData = this.response;

    this.response.$promise.then((response) => {

      objectData.isLoading = false;
      objectData.valid = true;
      this.formatsLfId = response.content;

      let originFormat = (response.content.concat(formats)).find(function(format){return format.id == self.media.format});

      this.formatsLfId = self.formatsLfId.filter(function(format){return format.definition == originFormat.definition});

    }, function(error) {
      // Si nos devuelve un 404, no queremos que se muestre el recurso, pero tampoco que nos tire error
      if(error.status == 404){
        objectData.valid = false;
        objectData.isLoading = false;
      }
    });
    return this.response;
  }

  updateAspectRatioList(){
    if(this.copy.media.type && this.copy.media.format){
      this.getAspectRatios(this.copy.media.format.concat('-').concat(this.copy.media.type).concat('-').concat(1));
    }
  }

  getAspectRatios(media){
    this.response = this.api['aspectsRatioMedias'].get({'id': media});
    this.response.isLoading = true;
    var objectData = this.response;

    this.response.$promise.then((response) => {

      objectData.isLoading = false;
      objectData.valid = true;
      this.aspectRatios = response.content;

    }, function(error) {
      // Si nos devuelve un 404, no queremos que se muestre el recurso, pero tampoco que nos tire error
      if(error.status == 404){
        objectData.valid = false;
        objectData.isLoading = false;
      }
    });
    return this.response;
  }

  _getEntityDependencies(){
    this.entityDependencies.forEach(
      it => {
        this.api[it.api].get(it.params).$promise.then(response => {
          this[it.model] = response.content;
        });
      });
  }

  getTitle(){
    return this.translateService.translate('results.tabs.segments.mainTitle');
  }

  inspectAspectRatio(){
    if(this.copy && this.copy.aspectRatio && this.copy.aspectRatio.id == 'undefined'){
      delete this.copy.aspectRatio.id;
    }
  }

  clone() {
    let promise = this.api['mediamateriallfid'].save({'id': this.$state.params.id}, this.copy).$promise;
    promise.then((response) => {
      this.alertService.success(
        {
          title: this.translate(this.successTitle),
          message: this.translate(this.successCreateMessage)
        }
      );
      this.$state.go('viewVirtualSegment', {id:this.$state.params.id,'editFirst':true});
      
      this.mdDialog.hide();
    }, (err) => {
      this.alertService.error({
        title: this.translate(this.errorTitle),
        message: err.data.message
      });
    });
  }

  _getPosition(str, m, i) {
    return str.split(m, i).join(m).length;
  };

  openCreateVirtualSegment(ev){
    let self = this;
    this.mdDialog
      .show(
      {
        controller: ($scope, $mdDialog) => {
          $scope.vm = self;
        },
        templateUrl: 'home/views/med.search.results.detail.segments.new.virtual.segment.html',
        parent: angular.element(document.body),
        targetEvent: ev,
        clickOutsideToClose: false
      }
    );
  }

  shouldSelectAspectRatioForLFID(){
    return (!this.mediaMaterial.aspectRatio && this.isAspectRatioRequired())
      || this.isNewFormatLFID();
  }

  isAspectRatioRequired(){
    if(!this.copy || !this.copy.media){
      return false;
    }
    return this.types.find(it => it.id == this.copy.media.type && it.mandatoryAspectRatio);
  }

  isInvalidCopy(){
    return !this.copy || !this.copy.media;
  }

  getFormat(format){
    let elem = this.formats.find(it => it.id === format);
    if(!elem) {
      return this.formatsLfId.find(it => it.id === format);
    }
    return elem;
  }

  isNewFormatLFID(){
    if(this.isInvalidCopy()){
      return false;
    }
    let currentFormat = this.getFormat(this.media.format);
    let newFormat = this.getFormat(this.copy.media.format);
    return currentFormat.definition !== newFormat.definition;
  }

  closeModal() {
    this.mdDialog.hide();
  }

  getSegmentBase(){
    return this.segmentsBase;
  }

  getSegmentBaseProperty(property){
    return this.segmentsBase[property];
  }

  _initSegment(){
    this.segmentsBase._initSegment();
  }

  _onSegmentCreate(){
    this.segmentsBase._onSegmentCreate();
  }

  _checkInitialSegment(){
    this.segmentsBase._checkInitialSegment();
  }

  _onSegmentEdit(){
    this.segmentsBase._onSegmentEdit()
  }

  _onSqueezeCreate(){
    this.segmentsBase._onSqueezeCreate();
  }

  _execute(){
    let self = this;
    this
      .get()
      .$promise
      .then((response) => {
        this.viewVirtualSegmentFlag.value = !this.entity.isVirtualSegment && this.entity.isMediaMaterialEnabled;
        this.showContextMenu.value = this.entity.isMediaMaterialEnabled;
        this.segmentsBase.segmentEditShow.value = this.entity.isMediaMaterialEnabled;
        this.segmentsBase.entity = this.entity;
        this.segmentTransformerService.segmentsToString(this.entity.items);
        this.segmentsBase.segments = this.entity.items;
        this.changeSegment(this.segmentsBase.segments[0]);

        this.segmentsBase.audit = {
          enabled: response && response.audit,
          entity: {audit: response.audit}
        };

      });
  }

  getAudit(){
    if(!this.entity){
      return false;
    }
    if(!this.entity.audit) {
      return false;
    }
    return this.segmentsBase.audit;
  }
  
  changeSegment(segment){
    this.segmentsBase.changeSegment(segment);
  }

  _allowCreateSqueeze(){
    this.segmentsBase._allowCreateSqueeze();
  }

  deleteEntry(entry){
    this.segmentsBase.deleteEntry(entry);
  }

  deleteSegment(index){
    this.segmentsBase.deleteSegment(index);
  }

  segmentCancel(){
    this.segmentsBase.segmentCancel(this);
  }

  reload(){
    this.segmentsBase.reload();
  }

  isValid(){
    return this.segmentsBase.isValid();
  }

  validateAndSave(form){
    form.$valid = this.isValid();
    this.segmentTransformerService.segmentsToObject(this.segmentsBase.segments);

    this
      .save(form)
      .then(()=> this.$state.go(this.$state.current, {}, {reload: true}) );
  }

  changeCreditType(segment){
    this.segmentsBase.changeCreditType(segment);
  }

  createCredit(segment){
    this.segmentsBase.createCredit(segment);
  }

  deleteSqueeze(squeeze){
    this.segmentsBase.deleteSqueeze(squeeze);
  }
  
  ingestIDChange(segment){
    if(!segment.ingestID){
      segment.isIngested = false;
    }
  }

}
